import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { API_CONFIG } from 'src/app/config/api.config';
import { EspecializacaoNewDTO } from '../../models/fichas/utils/especializacao-new.dto'

@Injectable({
  providedIn: 'root'
})
export class EspecializacaoService {

  constructor(private http: HttpClient) { }

  insert(dto: EspecializacaoNewDTO, idFicha: string, idAtributo: string): Observable<any> {
    const params = new HttpParams()
      .set('idFicha', idFicha)
      .set('idAtributo', idAtributo);

    return this.http.post(`${API_CONFIG.baseUrl}especializacao`,
      dto,
      {
        params
      }
    );
  }

  setFav(idEspecializacao: string, fav: string): Observable<any> {
    console.log(fav)
    const params = new HttpParams()
      .set('idEspecializacao', idEspecializacao)
      .set('fav', fav);

    return this.http.put(`${API_CONFIG.baseUrl}especializacao`,
      {

      },
      {
        params
      }
    );
  }

  delete(idFicha: string, idAtributo: string, idEspecializacao: string): Observable<any> {
    const params = new HttpParams()
      .set('idFicha', idFicha)
      .set('idAtributo', idAtributo)
      .set('idEspecializacao', idEspecializacao);
    console.log('ficha: ', idFicha, 'atrib: ', idAtributo, 'espec: ', idEspecializacao);
    return this.http.delete(`${API_CONFIG.baseUrl}especializacao`,
      {
        params
      }
    );
  }

}
