import { Injectable } from '@angular/core';
import { AtaqueDTO } from 'src/app/models/fichas/utils/ataque.dto';
import { DefesaDTO } from 'src/app/models/fichas/utils/defesa.dto';
import { AlertController } from '@ionic/angular';
import { CriarItemService } from '../criacoes/criar-item.service';

@Injectable({
  providedIn: 'root'
})
export class EquiparItemService {

  public ataquesEquipados: AtaqueDTO[] = [];
  public defesasEquipadas: DefesaDTO[] = [];

  constructor(private alertController: AlertController,
    private itemService: CriarItemService) {
    //  this.equiparAtaque(itemService.getItemById(1));
     }

  equiparAtaque(newArma: AtaqueDTO) {
    if (this.ataquesEquipados.length == 0) {
      this.ataquesEquipados.push(newArma);
      this.presentMessage(newArma.nome + " equipada com sucesso!");
    } else if (this.ataquesEquipados.length == 5) {
      console.log('primeiro if')
      this.presentMessage("N√∫mero m√°ximo de itens de ataque equipados atingido. Desequipe algum antes de continuar");
    } else {
      this.ataquesEquipados.forEach((arma, index) => {
        console.log('forEach')
        if (arma.id != newArma.id) {
          console.log('Segundo if')
          this.ataquesEquipados.push(newArma);
          this.presentMessage(newArma.nome + " equipada com sucesso!");
        } else if (arma.id == newArma.id) {
          console.log('Terceiro if')
          this.ataquesEquipados.splice(index, 1);
          this.presentMessage(newArma.nome + " desequipada com sucesso!")
        }
      });
    }
  }

  desequiparAtaque(newArma: AtaqueDTO) {
    if (this.ataquesEquipados.length == 0) {
      this.presentMessage("N√£o h√° itens de ataque equipados!");
    } else {
      this.ataquesEquipados.forEach((arma, index) => {
        if (arma.id != newArma.id) {
          this.presentMessage(arma.nome + " n√£o estava equipada.");
        } else if (arma.id == newArma.id) {
          this.ataquesEquipados.splice(index, 1);
          this.presentMessage(arma.nome + " desequipada com sucesso!")
        }
      });
    }
  }

  async presentMessage(mensagem: string) {
    const alert = await this.alertController.create({
      message: mensagem,
      header: "Sucesso!",
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          cssClass: 'secondary'
        }
      ]
    });
    await alert.present();
  }
}
