import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { API_CONFIG } from 'src/app/config/api.config';
import { NpcNewDTO } from 'src/app/models/Npc/npc_new.dto';
import { NpcDTO } from 'src/app/models/Npc/npc.dto';
import { InventarioUpdateDTO } from 'src/app/models/inventario/inventario-update.dto';

@Injectable({
  providedIn: 'root'
})
export class InventarioService {

  private tipo: any;

  setTipo(tipo) {
    this.tipo = tipo;
  }

  getTipo() {
    return this.tipo;
  }

  constructor(private http: HttpClient) { }

  findById(id: string): Observable<any> {
    return this.http.get(`${API_CONFIG.baseUrl}inventario/${id}`,
      {
        responseType: 'json',
        observe: "body"
      });
  }

  update(id: string, dto: InventarioUpdateDTO): Observable<any> {
    return this.http.put(`${API_CONFIG.baseUrl}inventario/${id}`, dto)
  }

  updateDinheiro(id: string, valor: string): Observable<any> {
    const p = new HttpParams()
      .set('dinheiro', valor);

    return this.http.put(`${API_CONFIG.baseUrl}inventario/dinheiro/${id}`, {}, { params: p });
  }

  findByIdInMesa(idInventario: string, idNpc: string, tipo: string, idMesa: string): Observable<any> {

    if (this.tipo)
      tipo = this.tipo;

    const params = new HttpParams()
      .set('idInventario', idInventario)
      .set('idNpc', idNpc)
      .set('tipo', tipo)
      .set('idMesa', idMesa);

    return this.http.get(`${API_CONFIG.baseUrl}mesa/inventario`,
      {
        responseType: 'json',
        observe: "body",
        params
      });
  }

  updateInMesa(dto: InventarioUpdateDTO, idInventario: string, idNpc: string, tipo: string, idMesa: string): Observable<any> {
    const params = new HttpParams()
      .set('idInventario', idInventario)
      .set('idNpc', idNpc)
      .set('tipo', tipo)
      .set('idMesa', idMesa);

    return this.http.put(`${API_CONFIG.baseUrl}mesa/inventario`,
      dto,
      {
        responseType: 'json',
        observe: "body",
        params
      });
  }

  updateDinheiroInMesa(dinheiro: string, idInventario: string, idNpc: string, tipo: string, idMesa: string): Observable<any> {
    const params = new HttpParams()
      .set('idInventario', idInventario)
      .set('dinheiro', dinheiro)
      .set('idNpc', idNpc)
      .set('tipo', tipo)
      .set('idMesa', idMesa);

    return this.http.put(`${API_CONFIG.baseUrl}mesa/dinheiro`,
      null,
      {
        responseType: 'json',
        observe: "body",
        params
      });
  }
}
